@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Poppins', sans-serif;
}


/* width */

::-webkit-scrollbar {
    width: 10px;
}


/* Track */

::-webkit-scrollbar-track {
    background: #555;
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: #888;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.cards {
    width: 100%;
    height: 100%;
    background-color: #2e2e2e;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 3px #ccc;
}

.cards h1 {
    color: #fff;
}

.cards p {
    color: #fff;
}

.userinput {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #2e2e2e;
    border-radius: 5px;
    box-shadow: 0 2px 3px #ccc;
}

.userinput input {
    width: 80%;
    border-radius: 3px;
    border: 1px solid #fff;
    padding: 8px;
}

.custom-jumbotron {
    margin: 30px 0;
}

.custom-jumbotron .display-3 {
    text-align: left;
}

.custom-jumbotron .lead {
    text-align: left;
    width: 80%;
}

.space-between {
    width: 100%;
    padding: 20px;
}

.img-wrapper {
    position: relative;
    overflow: hidden;
    border-radius: 16px !important;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px !important;
    transition: 0.3s ease;
    z-index: 10;
}

.img-wrapper:hover {
    transform: scale(1.03);
}

.img-wrapper .overlay-text {
    /* position: absolute;
    bottom: 0; */
    /* background-color: #000;
    opacity: 0.8; */
    padding: 10px 0;
    width: 100%;
}

.img-wrapper .overlay-text h1 {
    font-size: 20px;
    color: #111111;
    text-align: left;
    padding-left: 10px;
}

.img-wrapper .overlay-text p {
    font-size: 15px;
    color: #111111;
    margin-bottom: 0;
    text-align: left;
    padding-left: 10px;
    padding-bottom: 20px;
}

.wrap-around {
    width: 100%;
    padding: 10px;
}
